.o-content {

	width:100%;
	box-sizing: border-box;

	> * + * {
		margin-top:calc(1.4rem * #{$zoom});
		@media #{$respond} {
			margin-top: 1rem;
		}
	}

	&--md {
		> * + * {
			margin-top:calc(1rem * #{$zoom});
		}
	}

	&--sm {
		> * + * {
			margin-top:calc(0.75rem * #{$zoom});
		}
	}

	&--xs {
		> * + * {
			margin-top:calc(0.5rem * #{$zoom});
		}
	}

	/* ELEMENTS - why is this here?
	********************************************************************************/

	ul {
		margin:0;
		list-style-type: disc;
		padding-left: 1rem;
	}

	p {
		.c-button:first-child {
			margin-top:0.5rem;
		}
	}

	li, p {
		a:not(.c-button) {
			text-decoration: underline;
		}
	}

}
